___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-color: #f5f5f5;
    font-family: 'Roboto', sans-serif;
    color: cadetblue;
    padding: 40px;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

nav {
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#logo {
    flex: 1;
    font-family: "Nerko One", cursive;
    font-size: x-large;
    color: darkgoldenrod;
}

nav ul {
    flex: 3;
    list-style: none;
    display: flex;
    justify-content: space-around;
}

nav ul li button {
    margin: 20px;
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    color: #f5f5f5;
    font-size: larger;
    background-color: cadetblue;
    box-shadow: 6px 3px 5px rgba(0, 0, 0, 0.2);
}

nav ul li button:hover {
    background-color: darkcyan;
    box-shadow: 12px 6px 10px rgba(0, 0, 0, 0.2);
}

main {
    flex: 1;
}

#home {
    display: flex;
}

#text_area {
    flex: 3;
    padding: 40px;
}

#hero {
    padding: 50px;
    flex: 5;
}

img {
    width: 100%;
    aspect-ratio: 2 / 1;
    object-fit: cover;
    object-position: center;
    border-radius: 20px;
}

#menu-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.menu-item {
    flex:auto;
    width: 200px;
    height: 200px;
    text-align: center;
    margin: 20px;
    background-color: cadetblue;
    color: #f5f5f5;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 8px 4px 6px rgba(0, 0, 0, 0.2);
}

.card {
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 20px;
    max-width: 300px;
}

.card p {
    text-align: left;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,iCAAiC;IACjC,gBAAgB;IAChB,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,OAAO;IACP,iCAAiC;IACjC,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,OAAO;IACP,gBAAgB;IAChB,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,mBAAmB;IACnB,cAAc;IACd,iBAAiB;IACjB,2BAA2B;IAC3B,0CAA0C;AAC9C;;AAEA;IACI,0BAA0B;IAC1B,4CAA4C;AAChD;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,OAAO;IACP,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,OAAO;AACX;;AAEA;IACI,WAAW;IACX,mBAAmB;IACnB,iBAAiB;IACjB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,eAAe;AACnB;;AAEA;IACI,SAAS;IACT,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,2BAA2B;IAC3B,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,0CAA0C;AAC9C;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB","sourcesContent":["body {\n    background-color: #f5f5f5;\n    font-family: 'Roboto', sans-serif;\n    color: cadetblue;\n    padding: 40px;\n    display: flex;\n    flex-direction: column;\n    height: 100vh;\n}\n\nnav {\n    height: 100px;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n#logo {\n    flex: 1;\n    font-family: \"Nerko One\", cursive;\n    font-size: x-large;\n    color: darkgoldenrod;\n}\n\nnav ul {\n    flex: 3;\n    list-style: none;\n    display: flex;\n    justify-content: space-around;\n}\n\nnav ul li button {\n    margin: 20px;\n    padding: 10px 20px;\n    border: none;\n    border-radius: 10px;\n    color: #f5f5f5;\n    font-size: larger;\n    background-color: cadetblue;\n    box-shadow: 6px 3px 5px rgba(0, 0, 0, 0.2);\n}\n\nnav ul li button:hover {\n    background-color: darkcyan;\n    box-shadow: 12px 6px 10px rgba(0, 0, 0, 0.2);\n}\n\nmain {\n    flex: 1;\n}\n\n#home {\n    display: flex;\n}\n\n#text_area {\n    flex: 3;\n    padding: 40px;\n}\n\n#hero {\n    padding: 50px;\n    flex: 5;\